.container main{
    margin-bottom: 20px;
    margin-top: 60px;
}
.navbar{
    background-color: rgb(12, 13, 22) !important; 
}
.navbar form {
    margin: 10px 0;
}
.nav-link{
    font-size: 1em;
    color: #ffffff !important;
}
.navbar button {
    border-color: #fff;
    color: #fff;
}

.current {
    font-weight: bolder;
    background-color: #57575780;
    width: 75px;
    max-width: 75px;
    text-align: center;
}
.footer{
    background-color: #e6e6e6 !important; 
}
.footer .bi {
    font-size: larger;
}
.send-email .btn{
    border-color: #000000 !important;
    background-color: transparent !important;
    color: #000;
}
.send-email .btn:hover{
    background-color: #000 !important;
    color: #fff !important;
}

.video-container {
    position: relative;
    width: 100%;
    padding-bottom: 56.25%; /* Proporção 16:9 (315/560 = 0.5625 ou 56.25%) */
    height: 0;
    overflow: hidden;
    max-width: 100%;
}

.video-container iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
